<template>
    <div class="dc-select">
        <input
            class="dc-input-inner"
            type="text"
            readonly="readonly"
            autocomplete="off"
            :placeholder="placeholder"
            @click="click"
            :value="text"
        >
        <span class="dc-input__suffix">
      <span class="icon kedaIconfont dc-input-arrow">&#xe612;</span>
    </span>
    </div>
</template>

<script>
export default {
    name: 'DivisionCascadeSelect',
    props: {
        placeholder: String,
        level: String,
        text: String
    },
    methods: {
        click () {
            // const params = {level: this.level, ...selectedCounty};
            this.$emit('dcSelectClick', this.level);
        }
    }
};
</script>

<style scoped lang="scss">
    .dc-select {
        height: 72px;
        background: rgba(255, 255, 255, .8);
        border-radius: 6px;
        position: relative;

        .dc-input-inner {
            padding: 0 30px 0 15px;
            border: none;
            height: 100%;
            line-height: 100%;
            width: 100%;
            box-sizing: border-box;
            cursor: pointer;
            border-radius: 10px;

            &:focus {
                outline: none;
                outline-offset: 0;
            }
        }

        .dc-input__suffix {
            position: absolute;
            display: flex;
            height: 100%;
            right: 10px;
            width: 100%;
            top: 0;
            text-align: center;
            color: #c0c4cc;
            transition: all .3s;
            pointer-events: none;
            width: 30px;
            align-items: center;

            .dc-input-arrow {
                width: 100%;
                display: block;
                transition: .1s all;
            }
        }
    }

    .active {
        .dc-input-arrow {
            transform: rotate(180deg);
        }
    }
</style>
