</template>

<script>
import { mapMutations } from 'vuex';
import photoTips from '@/components/photo-tips';

export default {
    name: 'LiveHefeiPhotoTips',
    components: { photoTips },
    methods: {
        ...mapMutations('common', ['changeStep']),
        goUp () {
            this.changeStep('up');
        },
        goDown () {
            this.changeStep('down');
        }
    }
};
</script>

