<template>
    <div>
        <div class="kd-title-content-wrapper wrap-info">
            <div class="top">
                <div class="item">
                    <span class="label">姓名：</span>
                    <span class="message">{{ identifyCard.partyName }}</span>
                    <span class="label00">性别:</span>
                    <span class="message00">{{ identifyCard.gender === 1 ? '男' : '女' }}</span>
                </div>
            </div>
            <div class="detail">
                <div class="item">
                    <span class="label">身份证：</span>
                    <span class="message">{{ identifyCard.certNumber }}</span>
                </div>
            </div>
            <div class="detail">
                <div class="item">
                    <span class="label">户籍地址：</span>
                    <span class="message">{{ identifyCard.certAddress }}</span>
                </div>
            </div>
            <div class="detail">
                <div class="item">
                    <span class="label">现居地址：</span>
                    <div class="address-input">
                        <kd-full-input
                            placeholder="请输入地址"
                            :watch-data="getAddress"
                            :default-content="address"
                            :rows="5"
                            :is-texture="false"
                        />
                    </div>
                </div>
            </div>
            <div class="red-tip">
                {{ errTip }}
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="goNext"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { keyboardHide } from '@/service/common';

export default {
    name: 'LiveHefeiRegister',
    data () {
        return {
            address: '',
            errTip: ''
        };
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('liveHefei', ['overridePostForm']),
        goUp () {
            this.changeStep('up');
        },
        async goNext () {
            if (!this.address) {
                this.errTip = '请将信息填写完整';
                return false;
            }
            this.overridePostForm({ djdz: this.address });
            this.changeStep('down');
        },
        getAddress (data) {
            this.address = data;
        }
    },
    computed: {
        ...mapState('common', {
            identifyCard: 'identifyCard'
        })
    },
    beforeDestroy () {
        keyboardHide();
    }
};
</script>

<style lang="scss" scoped>
    .wrap-info {
        .top {
            height: 11.42vh;
            overflow: hidden;
            font-size: 2.8vh;
            line-height: 18.42vh;

            .item {
                float: left;
            }

            .label00 {
                color: #e0ebf796;
                padding: 0 0.6vw 0 3.6vw;
            }

            .message00 {
                color: #ffffff;
            }
        }

        .detail {
            height: 3.6vh;
            font-size: 2.8vh;
            line-height: 3.6vh;
            margin-top: 3.8vh;
        }

        .label {
            display: inline-block;
            color: #e0ebf796;
            width: 17vw;
            text-align: right;

        }

        .message {
            display: inline-block;
            color: #ffffff;
        }

        .address-input {
            width: 1080px - 1080*.17 - 100;
            display: inline-block;
            vertical-align: middle;
            color: black;
        }

        .red-tip {
            margin-left: 15vw;
            margin-top: 10vh;
            height: 15px;
            line-height: 15px;
            font-size: 20px;
            color: #ff2306;
        }
    }

</style>
