<template>
    <div class="dc-grid">
        <el-row :gutter="20">
            <el-col
                :span="8"
                v-for="o in gridArray"
                :key="o.code"
            >
                <div
                    class="dc-grid-item"
                    @click="click(o)"
                >
                    {{ o.name }}
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
export default {
    name: 'DivisionCascadeGrid',
    props: { gridArray: Array },
    methods: {
        click (o) {
            this.$emit('dcGridClick', o);
        }
    }
};
</script>

<style scoped lang="scss">

    .dc-grid {
        background: rgba(255, 255, 255, 0.6);
        border-radius: 6px;
        border: 1px solid rgba(242, 249, 255, 1);
        padding: 30px;
        height: 240px;
        overflow: hidden;

        .dc-grid-item {
            font-size: 26px;
            padding: 12px 0;
            cursor: pointer;
            text-overflow: ellipsis;
            word-break: keep-all;
            overflow: hidden;
        }
    }
</style>
