<template>
    <div>
        <div class="kd-page-title">
            {{ getTitle() }}
        </div>
        <div
            class="kd-page-content-wrapper wrap-info"
            v-if="step === 0"
        >
            <el-form
                class="uploadLabel"
                label-width="15vw"
            >
                <el-form-item label="材料上传：">
                    <div class="imgListsWrap">
                        <div
                            class="uploadImg"
                            v-for="(item,index) in photoList"
                            :key="index"
                        >
                            <img
                                class="preImg"
                                :src="item"
                                :key="index"
                            >
                            <i
                                @click="removeImg(index)"
                                class="delImg"
                            />
                        </div>
                        <div
                            class="uploadButton"
                            @click="goToPhoto"
                        >
                            <p :class="isLoad ? 'kedaIconfont kedaIcon-zhaoxiangji' : 'el-icon-loading'"/>
                        </div>
                    </div>
                </el-form-item>
            </el-form>
            <div class="footer">
                温馨提示：请上传{{ reasonTip }}照片
            </div>
        </div>
        <div
            class="kd-page-content-wrapper wrap-img"
            v-if="step === 1"
        >
            <img
                :src="highPhotoImg"
                alt=""
                width="400"
                height="300"
            >
        </div>

        <div
            class="kd-page-content-wrapper wrap-img"
            v-if="step === 2"
        >
            <img
                :src="nowImg"
                alt=""
                width="400"
                height="300"
            >
        </div>

        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                v-if="step === 0"
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                v-if="step === 0"
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="goUp"
            />
            <kd-button
                v-if="step === 0"
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="goNext"
                :but-disable="photoList.length === 0"
            />

            <kd-button
                v-if="step === 1"
                icon="kedaIcon-zhaoxiangji"
                text="拍照"
                :button-handle="startScan"
            />
            <kd-button
                v-if="step === 1"
                icon="kedaIcon-chexiao1"
                text="返回"
                :button-handle="goToIndex"
            />

            <kd-button
                v-if="step === 2"
                icon="kedaIcon-baocun"
                text="保存"
                :button-handle="save"
            />
            <kd-button
                v-if="step === 2"
                icon="kedaIcon-zhaoxiangji"
                text="重拍"
                :button-handle="goToPhoto"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { wsSend } from '../../../../utils/websocket';
import { cancelPrefix } from '../../../../utils/tools';

export default {
    name: 'LiveHefeiUploadReason',
    data () {
        return {
            step: 0,
            nowImg: '',
            isLoad: false,
            reasonTip: '', //  办理原因提示
            typeName: '', //  办理原因
            photoType: 0,
            photoList: [] // 免检材料照片(只能上传一张)
        };
    },
    computed: {
        ...mapState('permanent', {
            dialog: 'highPhotoDialog',
            lightWebSocket: 'lightWebSocket',
            highPhotoDev: 'highPhotoDev',
            highPhotoImg: 'highPhotoImg'
        }),
        ...mapState('liveHefei', {
            postForm: 'postForm',
            reasonPhotoList: 'reasonPhotoList'
        })
    },
    mounted () {
        this.getTipName();
        if (this.reasonPhotoList.length > 0) {
            this.isLoad = false;
            this.photoList = this.reasonPhotoList;
        }
        setTimeout(() => {
            this.isLoad = true;
        }, 3000);

        this.dialog.html_loaded('one');
    },
    beforeDestroy () {
        // 关闭高拍仪主视频
        this.dialog.get_actionType('closeSignal');
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('liveHefei', ['setReasonPhotoList', 'setJzzPhotos']),
        getTitle () {
            if (this.step === 0) {
                return '上传办理原因相关照片';
            } else if (this.step === 1) {
                return '请将身份证放置于高拍仪区域内';
            } else if (this.step === 2) {
                return '拍摄完成';
            }
        },
        // 温馨提示的话语
        getTipName () {
            if (this.postForm.sltj) {
                if (this.postForm.sltj === '1001') {
                    this.typeName = '房产证明';
                    this.photoType = 5;
                    this.reasonTip = '房产证（非本人房产需上传结婚证、户口本等证明）';
                } else if (this.postForm.sltj === '1002') {
                    this.typeName = '租房合同';
                    this.photoType = 3;
                    this.reasonTip = '租房合同（需包含房东房产信息和身份信息）';
                } else {
                    this.typeName = '就读证明';
                    this.photoType = 4;
                    this.reasonTip = '就读证明';
                }
            }
        },
        // 进入第一步
        async goToIndex () {
            wsSend(this.lightWebSocket, 'lightCtrl@B6:OFF');
            this.step = 0;
        },
        // 进入第二步
        goToPhoto () {
            if (this.isLoad) {
                wsSend(this.lightWebSocket, 'lightCtrl@B6:ON');
                this.step = 1;
            }
        },
        goUp () {
            this.changeStep('up');
        },
        goNext () {
            const arr = [];
            this.photoList.forEach((val, index) => {
                arr.push({
                    photoType: this.photoType,
                    typeName: this.typeName,
                    photo: cancelPrefix(val)
                });
            });
            this.setReasonPhotoList(this.photoList);
            this.setJzzPhotos(arr);
            this.changeStep('down');
        },
        // 拍照，进入第三部
        startScan () {
            this.nowImg = this.highPhotoImg;
            this.step = 2;
        },
        // 保存数据，并返回第一步
        save () {
            this.photoList.push(this.nowImg);
            wsSend(this.lightWebSocket, 'lightCtrl@B6:OFF');
            this.step = 0;
        },
        removeImg (index) {
            this.photoList.splice(index, 1);
            this.setReasonPhotoList(this.photoList);
        }

    }
};
</script>

<style lang="scss" scoped>
    .bg {
        width: 34.37vw;
        height: 26.56vh;
        border-radius: 0.5vh;
        background: url("../../../../assets/images/cardno_bg.png");
        background-size: 100% 100%;
        margin: 0 auto;
    }

    .wrap-img {
        display: flex;
        justify-content: center;
        align-items: center;

        img {
            width: 400px;
            height: 300px;
            background: #fff;
        }
    }

    .wrap-info {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 5vw 0;

        ::v-deep .el-form-item__content {
            margin-right: 10vw;
        }

        .imgListsWrap {
            display: flex;

            .uploadImg {
                margin-right: 20px;
                border-radius: 6px;
                border: 1PX solid rgba(242, 249, 255, 1);/*no*/
                width: 13vw;
                height: 13vw;
                position: relative;

                .preImg {
                    width: 100%;
                    height: 100%;
                    border-radius: 6px;
                }

                .delImg {
                    position: absolute;
                    top: -15px;
                    right: -15px;
                    z-index: 3;
                    width: 30px;
                    height: 30px;
                    background: url(../../../../assets/images/del.png);
                    background-size: 100% 100%;
                }
            }

        }

        .uploadButton {
            background: rgba(255, 255, 255, 0.8);
            border-radius: 6px;
            border: 1PX solid rgba(242, 249, 255, 1);/*no*/
            width: 13vw;
            height: 13vw;
            text-align: center;

            p {
                font-size: 5vh;
                color: #85BAF0;
                text-align: center;
                line-height: 13vw;
            }
        }

        .footer {
            font-size: 22px;
            font-weight: 400;
            line-height: 40px;
            margin-left: 3vw;
            width: 60vw;
        }
    }
</style>
