<template>
    <div class="vueCropperWrap">
        <div class="imgWrap" @click="getCropImg">
            <img alt="" class="takingImg" v-if="!isTaked" :src="photoSrc">
            <img alt="" class="takingImg" v-if="isTaked" :src="resultSrc">
            <img alt="" class="tipMask" v-if="!isTaked" :src="tipMask">
            <i v-if="active"><img alt="" :src="tick"></i>
        </div>
        <p class="time">{{ isWorking ? clockTime : '' }}</p>
        <vue-cropper
            v-if="takingSrc"
            class="vueCropper"
            :style="{width:width,height:height}"
            ref="cropper"
            :img="takingSrc"
            :output-size="option.outputSize"
            :output-type="option.outputType"
            :info="option.info"
            :can-scale="option.canScale"
            :auto-crop="option.autoCrop"
            :auto-crop-width="width"
            :auto-crop-height="height"
            :fixed="getFixed()"
            :fixed-number="size"
        />
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { VueCropper } from 'vue-cropper'; // 裁剪插件
import { PHOTO_SIZE } from '@/constant/constant';
import { wsSend } from '@/utils/websocket';
import tick from '@/assets/images/tick.png';
import tipMask from '@/assets/images/photo/tipMask.png';

export default {
    name: 'CropPhoto',
    components: { VueCropper },
    props: ['photoSrc', 'size', 'clickImg', 'id', 'takePhotoSrc', 'clearResPhoto'],
    computed: {
        ...mapState('permanent', {
            singleReflexWebSocket: 'singleReflexWebSocket'
        })
    },
    data () {
        return {
            tick: tick,
            tipMask: tipMask, // 拍照遮罩框
            active: false, // 选中哪一个
            takingSrc: '', // 需裁剪的原始照片
            resultSrc: '', // 裁剪之后的照片
            isWorking: false, // 是否正在拍照
            isTaked: false, // 是否裁剪完成
            waitImgBack: false, // 等待聚焦拍照之间
            clockTime: 3, // 倒计时间
            intervalObj: null, // 定时器
            width: PHOTO_SIZE.WIDTH,
            height: PHOTO_SIZE.HEIGHT,
            option: { // 裁剪框属性
                info: false, // 裁剪框的大小信息
                outputSize: 1, // 裁剪生成图片的质量
                outputType: 'jpeg', // 裁剪生成图片的格式
                canScale: false, // 图片是否允许滚轮缩放
                autoCrop: true, // 是否默认生成截图框
                canMoveBox: false, // 截图框能否拖动
                centerBox: true, // 截图框是否被限制在图片里面
                fixedBox: true
            }
        };
    },
    watch: {
        takePhotoSrc (newVal) {
            if (this.waitImgBack && newVal) {
                this.getPhoto(newVal);
            }
        }
    },
    methods: {
        ...mapMutations('permanent', ['setSingleReflexTakeImgSrc']),
        getFixed () {
            if (this.CONSTANT.disableTailoring) {
                return false; // 证件照前端不裁剪
            } else {
                return !!this.size; // 证件照前端裁剪
            }
        },
        // 获取裁剪之后的图片
        getCropImg () {
            this.clickImg(this.id, this.resultSrc);
        },
        // 改变选中状态
        changeActive (flag) {
            if (this.isTaked) {
                this.active = flag;
            }
        },
        // 拍照
        getImg () {
            this.active = false;
            this.clockTime = 3;
            this.isTaked = false;
            this.isWorking = true;
            this.intervalObj = setInterval(() => {
                this.clockTime--;
                if (this.clockTime === 0) {
                    clearInterval(this.intervalObj);
                    this.intervalObj = null;
                    this.isWorking = false;
                    this.waitImgBack = true;
                    wsSend(this.singleReflexWebSocket, {
                        module: 'Camera',
                        directive: 'takePhoto'
                    });
                }
            }, 1000);
        },
        // 截图
        getPhoto (img) {
            this.waitImgBack = false;
            this.takingSrc = img;
            if (this.CONSTANT.disableCompress) {
                this.isTaked = true;
                this.resultSrc = this.takingSrc;
                this.$emit('startPhoto', this.id + 1);
            } else {
                setTimeout(() => {
                    this.$refs.cropper.getCropData((data) => {
                        this.isTaked = true;
                        this.resultSrc = data;
                        this.$emit('startPhoto', this.id + 1);
                    });
                }, 1000);
            }
        }
    },
    beforeDestroy () {
        clearInterval(this.intervalObj);
    }

};
</script>

<style scoped lang="scss">
    .vueCropperWrap {
        width: calc(35vh / 1056 * 704);

        .vueCropper {
            pointer-events: none;
            visibility: hidden;
        }

        .imgWrap {
            width: 100%;
            height: 35vh;
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
            border: 1PX solid #ffffff; /*no*/
            background: #ffffff;

            .takingImg {
                background: url("../assets/images/loading.gif") center no-repeat;
                max-height: 100%;
                max-width: 100%;
            }

            > i {
                position: absolute;
                width: 30px;
                height: 30px;
                bottom: -50PX; /*no*/
                img {
                    width: 100%;
                    height: 100%;
                }
            }

            .tipMask {
                width: 100%;
                height: 100%;
                position: absolute;
                z-index: 99;
                left: 0;
                top: 0;
                padding: 25px;
                box-sizing: border-box;
            }
        }

        .time {
            font-size: 50px;
            height: 50px;
            width: 100%;
            text-align: center;
            color: #FFFFFF;
        }
    }
</style>
