<template>
    <div class="dc-container">
        <div class="dc-caption">
            <span>请选择行政区域</span>
        </div>
        <el-row :gutter="20">
            <el-col :span="8">
                <division-cascade-select
                    :class="currentLevel==='county'?'active':''"
                    placeholder="请选择区县"
                    level="county"
                    @dcSelectClick="selectClick"
                    :text="value.selectedCounty.name"
                />
            </el-col>
            <el-col :span="8">
                <division-cascade-select
                    :class="currentLevel==='town'?'active':''"
                    placeholder="请选择乡镇街道"
                    level="town"
                    @dcSelectClick="selectClick"
                    :text="value.selectedTown.name"
                />
            </el-col>
            <el-col :span="8">
                <division-cascade-select
                    placeholder="请选择居委会"
                    :class="currentLevel==='village'?'active':''"
                    level="village"
                    @dcSelectClick="selectClick"
                    :text="value.selectedVillage.name"
                />
            </el-col>
        </el-row>
        <el-row style="margin-top: 24px">
            <el-col :span="24">
                <division-cascade-grid
                    :grid-array="gridArray"
                    @dcGridClick="gridClick"
                />
            </el-col>
        </el-row>
    </div>
</template>

<script>
import divisionCascadeSelect from './division-cascade-select';
import divisionCascadeGrid from './division-cascade-grid';

export default {
    name: 'DivisionCascadeContainer',
    components: {
        divisionCascadeSelect,
        divisionCascadeGrid
    },
    props: {
        divisionSupplier: Function,
        errorHandle: Function,
        value: Object
    },
    data () {
        return {
            selectedDcArray: [],
            gridArray: [],
            currentLevel: ''
        };
    },
    methods: {
        async selectClick (level) {
            if (level === this.currentLevel) {
                return;
            }
            this.currentLevel = level;
            if (level === 'county') {
                this.gridArray = await this.divisionSupplier('');
            }
            if (level === 'town') {
                this.gridArray = await this.divisionSupplier(this.value.selectedCounty.code);
            }
            if (level === 'village') {
                this.gridArray = await this.divisionSupplier(this.value.selectedTown.code);
            }
        },
        async gridClick (division) {
            if (this.currentLevel === 'county' && this.value.selectedCounty.code !== division.code) {
                // this.selectedCounty = division;
                this.value.selectedCounty = division;
                this.value.selectedTown = {};
                this.value.selectedVillage = {};
                // this.selectedTown = {};
                // this.selectedVillage = {};
                // 获取对应的town
                this.gridArray = await this.divisionSupplier(division.code);
                this.currentLevel = 'town';
            } else if (this.currentLevel === 'town' && this.value.selectedTown.code !== division.code) {
                // this.selectedTown = division;
                this.value.selectedTown = division;
                this.value.selectedVillage = {};
                // this.selectedVillage = {};
                // 获取对应的town
                this.gridArray = await this.divisionSupplier(division.code);
                this.currentLevel = 'village';
            } else if (this.currentLevel === 'village' && this.value.selectedVillage.code !== division.code) {
                // this.selectedVillage = division;
                this.value.selectedVillage = division;
            }
        }
    }
};
</script>

<style scoped lang="scss">
    .dc-container {
        .dc-caption {
            height: 37px;
            font-size: 26px;
            font-weight: 400;
            color: rgba(255, 255, 255, 1);
            line-height: 37px;
            margin-bottom: 20px;
        }

    }
</style>
