<template>
    <div>
        <div class="kd-page-title">
            照片采集
        </div>
        <div class="kd-page-content-wrapper">
            <chooseTakePhoto :return-photo="returnPhoto"/>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="goUp"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="goNext"
                :but-disable="!chosePhoto"
            />
        </div>
    </div>
</template>

<script>
import { mapMutations } from 'vuex';
import chooseTakePhoto from '@/components/choose-take-photo';
import { cancelPrefix, addPrefix } from '@/utils/tools';
import { idPicHandle } from '@/service/common';

export default {
    name: 'LiveHefeiTakePhoto',
    components: { chooseTakePhoto },
    data () {
        return {
            chosePhoto: ''
        };
    },
    methods: {
        ...mapMutations('identificationPhoto', ['setPreviewPrintPhoto']),
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        returnPhoto (chosePhoto) {
            this.chosePhoto = chosePhoto;
        },
        goUp () {
            this.changeStep('up');
        },
        async goNext () {
            const res = await idPicHandle({
                base64: cancelPrefix(this.chosePhoto),
                bgColor: 'white'
            });
            if (res && res.code === 1) {
                this.setPreviewPrintPhoto(addPrefix(res.obj));
                this.changeStep('down');
            } else {
                this.changeTipPage({
                    message: '出了点小意外，请返回重试！',
                    enableBack: true
                });
            }
        }
    }
};
</script>

