<template>
    <div>
        <div class="kd-title-content-wrapper wrap-info">
            <el-form
                class="uploadLabel"
                label-width="15vw"
            >
                <el-form-item
                    label="办理种类："
                    style="margin-bottom: 10vh"
                >
                    <el-radio-group v-model="drawType">
                        <el-radio
                            class="uploadChoose"
                            v-for="(item,index) in drawTypes"
                            :key="index"
                            :label="item.type"
                            border
                        >
                            {{ item.value }}
                        </el-radio>
                    </el-radio-group>
                </el-form-item>
                <div v-show="drawType===1">
                    <el-form-item
                        class="mailWrapper"
                        label="领取地址："
                    >
                        <span class="el-form-item__label">请到居住地所在派出所领取</span>
                    </el-form-item>
                    <el-form-item
                        class="mailWrapper"
                        label="联系电话："
                    >
                        <kd-full-input
                            :ime-type="3"
                            placeholder="请输入联系电话"
                            :watch-data="getPhone"
                            :default-content="this.postForm.sjrdh"
                            :limit-number="11"
                        />
                    </el-form-item>
                </div>
                <div v-show="drawType===2">
                    <el-form-item
                        class="mailWrapper"
                        label="收件人："
                    >
                        <kd-full-input
                            placeholder="请输入收件人"
                            :watch-data="getReceiver"
                            :default-content="this.postForm.sjrxm"
                        />
                    </el-form-item>
                    <el-form-item
                        class="mailWrapper"
                        label="联系电话："
                    >
                        <kd-full-input
                            :ime-type="3"
                            placeholder="请输入联系电话"
                            :watch-data="getPhone"
                            :default-content="this.postForm.sjrdh"
                            :limit-number="11"
                        />
                    </el-form-item>
                    <el-form-item
                        class="mailWrapper"
                        label="收件地址："
                    >
                        <kd-full-input
                            placeholder="请输入收件地址"
                            :watch-data="getAddress"
                            :default-content="this.postForm.yjdz"
                        />
                    </el-form-item>
                </div>
                <div
                    class="error-tip"
                    v-show="errorTip"
                >
                    {{ errorTip }}
                </div>
            </el-form>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="goNext"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations, mapActions } from 'vuex';
import { keyboardHide } from '@/service/common';

export default {
    name: 'LiveHefeiReceiveWay',
    data () {
        return {
            drawType: 1,
            // phone: this.postForm.sjrdh || '',
            // address: this.postForm.yjdz || '',
            // receiver: this.postForm.sjrxm || '',
            errorTip: '',
            drawTypes: [
                {
                    type: 1,
                    value: '自取'
                },
                {
                    type: 2,
                    value: '邮寄'
                }
            ]
        };
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('liveHefei', ['setSjrdh', 'setMailInfo', 'overridePostForm']),
        ...mapActions('liveHefei', ['submitResidencePermit']),
        goUp () {
            this.changeStep('up');
        },
        async goNext () {
            // 验证字段是否存在
            if (!this.validatePhone()) {
                return;
            }

            const { yjdz: address, sjrxm: receiver } = this.postForm;

            if (this.drawType === 2) {
                // 邮寄
                if (!address) {
                    this.errorTip = '请输入邮寄地址';
                    return;
                }
                if (!receiver) {
                    this.errorTip = '请输入收件人姓名';
                    return;
                }
            }
            this.errorTip = '';

            this.overridePostForm({ lzfs: this.drawType });

            const result = await this.submitResidencePermit();
            if (typeof (result) === 'string') {
                this.changeTipPage({ message: result });
            } else {
                this.changeStep('down');
            }
        },
        validatePhone () {
            const phone = this.postForm.sjrdh;
            if (!phone) {
                this.errorTip = '请输入手机号';
                return false;
            }
            if (!(/^[1][3,4,5,7,8,9][0-9]{9}$/.test(phone)) || phone.length !== 11) {
                this.errorTip = '请检查手机号是否正确';
                return false;
            }
            return true;
        },
        getPhone (value) {
            this.overridePostForm({
                sjrdh: value
            });
        },
        getAddress (value) {
            this.overridePostForm({
                yjdz: value
            });
        },
        getReceiver (value) {
            this.overridePostForm({
                sjrxm: value
            });
        }
    },
    computed: {
        ...mapState('liveHefei', {
            postForm: 'postForm'
        })
    },
    beforeDestroy () {
        keyboardHide();
    }
};
</script>

<style lang="scss" scoped>
    .wrap-info {
        padding-top: 5vw;
        padding-right: 4vw;

        .error-tip {
            color: red;
            font-size: 25px;
            margin-left: 50px;
        }

        .top {
            height: 11.42vh;
            overflow: hidden;
            font-size: 2.8vh;
            line-height: 18.42vh;

            .item {
                float: left;
            }

            .label00 {
                color: #e0ebf796;
                padding: 0 0.6vw 0 3.6vw;
            }

            .message00 {
                color: #ffffff;
            }
        }

        .detail {
            height: 3.6vh;
            font-size: 2.8vh;
            line-height: 3.6vh;
            margin-top: 3.8vh;
        }

        .label {
            display: inline-block;
            color: #e0ebf796;
            width: 17vw;
            text-align: right;

        }

        .message {
            display: inline-block;
            color: #ffffff;
        }

        .footer {
            box-sizing: border-box;
            padding: 8vh 5vh 5vh 5vh;
            font-size: 2.2vh;
            color: #e0ebf796;
            line-height: 4vh;

        }

    }

</style>
