<template>
    <div>
        <identify-card-page v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"/>
        <face-yitu v-if="currentStep === steps.findIndex(step=>step.key === 'faceYitu')"/>
        <contrast v-if="currentStep === steps.findIndex(step=>step.key === 'contrast')"/>
        <process
            :type="1"
            v-if="currentStep === steps.findIndex(step=>step.key === 'process')"
            :handle-business="handleBusiness"
        />
        <division v-if="currentStep === steps.findIndex(step=>step.key === 'division')"/>
        <register v-if="currentStep === steps.findIndex(step=>step.key === 'register')"/>
        <handle-type v-if="currentStep === steps.findIndex(step=>step.key === 'handleType')"/>
        <upload v-if="currentStep === steps.findIndex(step=>step.key === 'materialIdentity')"/>
        <upload-reason v-if="currentStep === steps.findIndex(step=>step.key === 'materialContract')"/>
        <photo-tips v-if="currentStep === steps.findIndex(step=>step.key === 'photoTip')"/>
        <take-photo v-if="currentStep === steps.findIndex(step=>step.key === 'takePhoto')"/>
        <collect-complete v-if="currentStep === steps.findIndex(step=>step.key === 'collectComplete')"/>
        <receive-way v-if="currentStep === steps.findIndex(step=>step.key === 'receiveWay')"/>
        <complete
            v-if="currentStep === steps.findIndex(step=>step.key === 'complete')"
            :data="responseBody"
            :foot-texts="footTexts"
        />
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import faceYitu from '@/view/public/before-work/face-yitu';
import contrast from '@/view/public/before-work/contrast';
import register from './components/register';
import division from './components/division';
import handleType from './components/handle-type';
import upload from './components/upload-identity';
import uploadReason from './components/upload-reason';
import receiveWay from './components/receive-way';
import complete from '@/components/complete';
import process from '@/components/process';
import photoTips from './components/photo-tips';
import takePhoto from './components/take-photo';
import collectComplete from './components/collect-complete';
// import {checkStatusByUerRecognitionIdAndIdNum, liveFaceRecognition} from '@/service/human/people';
import { faceResidencePermit } from '@/service/human/live';

export default {
    name: 'LiveHefeiIndex',
    components: {
        identifyCardPage,
        faceYitu,
        contrast,
        handleType,
        division,
        register,
        upload,
        receiveWay,
        photoTips,
        complete,
        process,
        takePhoto,
        collectComplete,
        uploadReason
    },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep',
            identifyCard: 'identifyCard',
            binocularCamera: 'binocularCamera'
        }),
        ...mapState('liveHefei', {
            responseBody: 'responseBody'
        })
    },
    data () {
        return {
            footTexts: ''
        };
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage', 'setSteps']),
        async handleBusiness () {
            // 保存人脸识别信息
            try {
                const res = await faceResidencePermit({
                    idNum: this.identifyCard.certNumber,
                    identifiedPhoto: this.binocularCamera
                });

                if (res && res.code === '0') {
                    const data = res.result;
                    // 业务类型 (01：申领居住证，05：居住登记，06：居住登记并申领居住证)
                    if (data && data.ywlx === '06') {
                        // 未登记过,需要登记
                        this.changeStep('down');
                    } else if (data && data.ywlx === '01') {
                        // 已登记过
                        this.changeStep('handleType');
                    }
                } else if (res) {
                    this.changeTipPage({ message: res.message });
                }
            } catch (e) {
                this.changeTipPage({ message: e.message() });
            }
        }
    },
    mounted () {
        this.setSteps([
            {
                name: '身份识别',
                isActive: true,
                visible: true,
                key: 'identifyCardPage'
            },
            {
                name: '身份识别',
                isActive: false,
                visible: false,
                key: 'faceYitu'
            },
            {
                name: '身份识别',
                isActive: false,
                visible: false,
                key: 'contrast'
            },
            {
                name: '身份识别',
                isActive: false,
                visible: false,
                key: 'process'
            },
            {
                name: '居住登记',
                isActive: false,
                visible: true,
                key: 'division'
            },
            {
                name: '居住登记',
                isActive: false,
                visible: false,
                key: 'register'
            },
            {
                name: '选择种类',
                isActive: false,
                visible: true,
                key: 'handleType'
            },
            {
                name: '提交资料',
                isActive: false,
                visible: true,
                key: 'materialIdentity'
            },
            {
                name: '提交资料',
                isActive: false,
                visible: false,
                key: 'materialContract'
            },
            {
                name: '照片采集',
                isActive: false,
                visible: true,
                key: 'photoTip'
            },
            {
                name: '照片采集',
                isActive: false,
                visible: false,
                key: 'takePhoto'
            },
            {
                name: '照片采集',
                isActive: false,
                visible: false,
                key: 'collectComplete'
            },
            {
                name: '领取方式',
                isActive: false,
                visible: true,
                key: 'receiveWay'
            },
            {
                name: '完成提交',
                isActive: false,
                visible: true,
                key: 'complete'
            }
        ]);
    }
};

</script>
