<template>
    <div>
        <div class="kd-page-title">
            照片采集
        </div>
        <div class="kd-page-content-wrapper collect-complete-box">
            <div class="chose-img">
                <img
                    :src="previewPrintPhoto"
                    alt=""
                >
                <!--<img src="../../../../assets/images/cameraDefault.png" alt="">-->
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleleft"
                text="重拍"
                :button-handle="reTakephoto"
            />
            <kd-button
                icon="kedaIcon-circleleft"
                text="下一步"
                :button-handle="goNext"
            />
        </div>
    </div>
</template>
<script>
import { mapState, mapMutations } from 'vuex';
import { cancelPrefix } from '@/utils/tools';
import cameraDefault from '../../../../assets/images/default/cameraDefault.png';

export default {
    name: 'LiveHefeiCollectComplete',
    data () {
        return {
            cameraDefault: cameraDefault
        };
    },
    computed: {
        ...mapState('identificationPhoto', {
            previewPrintPhoto: 'previewPrintPhoto'
        })
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('liveHefei', ['overridePostForm']),
        reTakephoto () { // 重拍
            this.changeStep('up');
        },
        goNext () {
            this.overridePostForm({
                certificatePhoto: cancelPrefix(this.previewPrintPhoto)
            });
            this.changeStep('down');
        }
    }
};
</script>

<style lang="scss" scoped>
    .collect-complete-box {
        display: flex;
        justify-content: center;
        align-items: center;

        .chose-img {
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 20px;
            box-sizing: border-box;
            width: 100%;
            height: 100%;

            img {
                max-width: 100%;
                max-height: 100%;
            }
        }
    }
</style>
