<template>
    <div>
        <div class="kd-page-title">
            请调整好姿势
        </div>
        <div class="kd-page-content-wrapper">
            <div class="photo-collection-box">
                <div class="left">
                    <div class="title">
                        <p>拍照须知</p>
                    </div>
                    <div class="desc">
                        <p>1.坐姿端正，双肩水平，平视镜头；</p>
                        <p>2.不得化浓妆，脸部表情自然，眼睛自然睁开；</p>
                        <p>3.头发不得遮挡眼睛，露出双耳，不得使用头部覆盖物；</p>
                    </div>
                    <div class="img">
                        <img
                            :src="correctPosture"
                            alt=""
                        >
                        <img
                            :src="wrongPosture"
                            alt=""
                        >
                    </div>
                </div>
                <div class="center">
                    <img :src="nowAvatar">
                </div>
                <div class="right">
                    <div class="title">
                        <p>温馨提示</p>
                    </div>
                    <div class="desc">
                        <p>您可按机器上的上下箭头调整座椅高度。</p>
                    </div>
                    <div class="img">
                        <img
                            :src="machine"
                            alt=""
                        >
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { wsSend } from '@/utils/websocket';
import correctPosture from '@/assets/images/correctPosture.png';
import wrongPosture from '@/assets/images/wrongPosture.png';
import machine from '@/assets/images/machine.png';
import audioObj from '@/assets/media/common/8.mp3';
import { playAudio } from '@/utils/media';

export default {
    name: 'PhotoTips',
    data () {
        return {
            correctPosture: correctPosture,
            wrongPosture: wrongPosture,
            machine: machine
        };
    },
    computed: {
        ...mapState('permanent', {
            nowAvatar: 'singleReflexImgSrc',
            singleReflexWebSocket: 'singleReflexWebSocket',
            lightWebSocket: 'lightWebSocket'
        })
    },
    mounted () {
        this.audioObj = playAudio(audioObj);
        this.audioObj.play();

        this.setSingleReflexImgSrc('');
        wsSend(this.singleReflexWebSocket, {
            module: 'Camera',
            directive: 'reOpen'
        });
        wsSend(this.singleReflexWebSocket, {
            module: 'Camera',
            directive: 'start'
        });
        wsSend(this.lightWebSocket, 'lightCtrl@B5:ON');
    },
    beforeDestroy () {
        this.audioObj.destroy();
    },
    methods: {
        ...mapMutations('permanent', ['setSingleReflexImgSrc'])
    }
};
</script>

<style scoped lang="scss">
    .photo-collection-box {
        display: flex;
        justify-content: space-around;
        padding: 40px 32px;

        .left {
            width: 320px;

            .title {
                font-size: 26px;
                margin-bottom: 20px;
            }

            .desc {
                font-size: 20px;
                margin-bottom: 24px;

                p {
                    margin-bottom: 4px;
                    line-height: 28px;
                }
            }

            .img {
                display: flex;

                img {
                    margin: 0 10px;
                    max-width: 100%;
                    max-height: 100%;
                }
            }
        }

        .center {
            text-align: center;
            width: 300px;
            height: 428px;
            overflow: hidden;
            display: flex;
            justify-content: center;
            vertical-align: center;

            img {
                background: url("../assets/images/loading.gif") center no-repeat;
                height: 100%;
                width: 100%;
            }
        }

        .right {
            width: 300px;

            .title {
                font-size: 26px;
                margin-bottom: 20px;
            }

            .desc {
                font-size: 20px;
                margin-bottom: 30px;

                p {
                    margin-bottom: 4px;
                    line-height: 28px;
                }
            }

            .img {
                display: flex;

                img {
                    margin: 0 20px;
