<template>
    <div>
        <div class="kd-title-content-wrapper wrap-info">
            <division-cascade-container
                :division-supplier="getDivision"
                v-model="selectedDivision"
            />
            <div
                class="error-tip"
                v-show="errorTip"
            >
                {{ errorTip }}
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="goNext"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { getDivisionList } from '@/service/human/live';
import divisionCascadeContainer from '@/components/division-cascade/division-cascade-container';

export default {
    name: 'LiveHefeiDivision',
    components: { divisionCascadeContainer },
    data () {
        return {
            address: '',
            errorTip: '',
            selectedDivision: {
                selectedCounty: {},
                selectedTown: {},
                selectedVillage: {}
            }
        };
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('liveHefei', ['overridePostForm']),
        goUp () {
            this.changeStep('up');
        },
        async goNext () {
            // 判断是否已填写
            const { selectedCounty, selectedTown, selectedVillage } = this.selectedDivision;
            if (!selectedCounty.code || !selectedCounty.name) {
                this.errorTip = '请选择区县';
                return;
            }
            if (!selectedTown.code || !selectedTown.name) {
                this.errorTip = '请选择乡镇街道';
                return;
            }
            if (!selectedVillage.code || !selectedVillage.name) {
                this.errorTip = '请选择居委会';
                return;
            }

            this.overridePostForm({
                qxid: selectedCounty.code, /* 区县ID */
                qxmc: selectedCounty.name, /* 区县名称 */
                xzjdid: selectedTown.code, /* 乡镇街道ID */
                xzjdmc: selectedTown.name, /* 乡镇街道名称 */
                jwhid: selectedVillage.code, /* 居委会id */
                jwhmc: selectedVillage.name
            });
            this.changeStep('down');
        },
        getAddress (data) {
            this.address = data;
        },
        async getDivision (pid) {
            const res = await getDivisionList({ id: pid });
            if (res.code !== '0') {
                this.changeTipPage({ message: res.message });
                return;
            }
            const list = res.result;

            if (!list || list.length === 0) {
                this.changeTipPage({ message: '不存在行政区划' });
                return;
            }
            if (!pid) {
                return this.getDivision(list[0].id);
            }
            return list.map(o => {
                return {
                    code: o.id,
                    name: o.jc
                };
            });
        }
    },
    computed: {
        ...mapState('common', {
            identifyCard: 'identifyCard'
        })
    }
};
</script>

<style lang="scss" scoped>
    .wrap-info {
        padding: 40px 40px 0 40px;

        .error-tip {
            color: red;
            font-size: 25px;
            margin-top: 5px;
        }

        .top {
            height: 11.42vh;
            overflow: hidden;
            font-size: 2.8vh;
            line-height: 18.42vh;

            .item {
                float: left;
            }

            .label00 {
                color: #e0ebf796;
                padding: 0 0.6vw 0 3.6vw;
            }

            .message00 {
                color: #ffffff;
            }
        }

        .detail {
            height: 3.6vh;
            font-size: 2.8vh;
            line-height: 3.6vh;
            margin-top: 3.8vh;
        }

        .label {
            display: inline-block;
            color: #e0ebf796;
            width: 17vw;
            text-align: right;

        }

        .message {
            display: inline-block;
            color: #ffffff;
        }

        .address-input {
            width: 1080px - 1080*.17 - 100;
            display: inline-block;
            vertical-align: middle;
            color: black;
        }

        .red-tip {
            margin-left: 15vw;
            margin-top: 10vh;
            height: 15px;
            line-height: 15px;
            font-size: 20px;
            color: #ff2306;
        }
    }

</style>
